import React from "react";
import moveForwardImg from "../../styles/assets/images/Hero-logo.avif";
import { Country } from "country-state-city";
import { useStaticQuery, graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { collection, addDoc, getFirestore } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const MoveForward = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const data = useStaticQuery(graphql`
    query MyQuery {
      site {
        id
        siteMetadata {
          firestore {
            apiKey
            appId
            authDomain
            messagingSenderId
            projectId
            storageBucket
          }
        }
      }
    }
  `);
  // Initialize Firebase
  console.log("data::", data);

  const app = initializeApp({ ...data.site.siteMetadata.firestore });
  const db = getFirestore(app);

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const clients = collection(db, "clients");
    await addDoc(clients, values);
    utils.showSuccssMsg("We will contact/talk to you soon");
    reset()
  };

  return (
    <section className="section-cta p-5">
      <div
        className="container p-5 col-md-12 bg-white shadow rounded-5"
        style={{
          background: `linear-gradient(to right bottom, #3c267d, #c27492)`,
        }}
      >
        <div className="row">
          <div className="col-md-6  col-12 p-5">
            <h2 className="text-uppercase text-white">
              BOOK A QUICK CONSULTATION
            </h2>
            <p className="text-white">
              Increase efficency, productivity, and business performance with
              Flipflop various products that have been used by tens of thousands
              of businesses in the word.
            </p>
            <form onSubmit={handleSubmit(handleOnSubmit)} className="cta-form">
              <div>
                <label for="full-name">Name</label>
                <input
                  id="full-name"
                  type="text"
                  placeholder="Enter Your Name"
                  {...register("name")}
                  maxLength={30}
                />
                <p className="text-danger mb-0">
                  {errors.name?.message}
                </p>
              </div>
              <div>
                <label for="email">Email address</label>
                <input
                  id="email"
                  type="email"
                  placeholder="me@example.com"
                  {...register("email")}
                  maxLength={50}
                />
                <p className="text-danger mb-0">
                  {errors.email?.message}
                </p>
              </div>
              <div>
                <label for="select-where">Where did you hear from us?</label>
                <select id="select-where" {...register("country")}>
                  <option value="">Please choose one option:</option>
                  {countries.map((ele, ind) => {
                    return (
                      <option key={ind} value={ele.isoCode}>
                        {ele.name}
                      </option>
                    );
                  })}
                </select>
                <p className="text-danger mb-0">
                  {errors.country?.message}
                </p>
              </div>
              <div>
                <label for="mobile">Skype / Whatsapp / Mobile</label>
                <input
                  id="mobile"
                  type="tel"
                  placeholder="Skype / Whatsapp / Mobile"
                  {...register("mobile")}
                  maxLength={50}
                />
                <p className="text-danger mb-0">
                  {errors.mobile?.message}
                </p>
              </div>
              <div>
                <label for="content">Content</label>
                <textarea
                  id="content"  
                  placeholder="Type content here"
                  rows={5}
                  className="input"
                  {...register("content")}
                  maxLength={200}
                ></textarea>
                <p className="text-danger mb-0">
                  {errors.content?.message}
                </p>
              </div>
              <button className="btn btn--form">Submit</button>
            </form>
          </div>
          <div className="col-md-6 p-5 d-flex flex-column align-items-center justify-content-center">
            <img
              className="w-auto"
              src={moveForwardImg}
              alt="move-forward-img"
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default MoveForward;
